import api from '../baseUrlTake';
import guid from '../utils/guid';

const CreateTicket =   (key, user, team ) => {

    return api.post('/commands', {

        "id": guid(),
        "to": "postmaster@desk.msging.net",
        "method": "set",
        "uri": "/tickets",
        "type": "application/vnd.iris.ticket+json",
        "resource": {
            "customerIdentity": user,
            "team": team
        }

    }, {
        headers: {
            'Content-Type': 'application/json',
            'Authorization': key
        }
    });
}

const TicketOpen = (key) => {

    return api.post('/commands', {
        "id": guid(),
        "to": "postmaster@desk.msging.net",
        "method": "get",
        "uri": "/monitoring/open-tickets?version=2"
      }, {
        headers: {
            'Content-Type': 'application/json',
            'Authorization': key
        }
    });
}

const TicketWaiting = (key) => {

    return api.post('/commands', {
        "id": guid(),
        "to": "postmaster@desk.msging.net",
        "method": "get",
        "uri": "/monitoring/waiting-tickets?version=2"
      }, {
        headers: {
            'Content-Type': 'application/json',
            'Authorization': key
        }
    });
}

const TicketClose =   (key, ticketId) => {

    return api.post('/commands', {
        "id": guid(),
        "to": "postmaster@desk.msging.net",
        "method": "set",
        "uri": "/tickets/change-status",
        "type": "application/vnd.iris.ticket+json",
        "resource": {
            "id": ticketId,
            "status": "ClosedAttendant",
            "tags": ["Transferncia de ticket"]
        }
    }, {
        headers: {
            'Content-Type': 'application/json',
            'Authorization': key
        }
    });
}
const TransferTicket = (key, ticketId, team) => {

        return api.post('/commands',  {
            "id": "{{$guid}}",
            "to": "postmaster@desk.msging.net",
            "method": "set",
            "uri": `/tickets/${ticketId}/transfer`,
            "type": "application/vnd.iris.ticket+json",
            "resource": {
                "team": team
            }
        }, {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': key
            }
        });
    }

   


export default {CreateTicket, TicketOpen, TicketClose, TransferTicket, TicketWaiting}